body {
  background-color: #000;
  color: #d4af37;
  font-family: Arial, sans-serif;
}

nav {
  background-color: #d4af37;
  padding: 1em;
  color: #000;
  text-align: center;
}

footer {
  background-color: #d4af37;
  padding: 1em;
  color: #000;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  padding: 1em;
}

.product {
  border: 1px solid #d4af37;
  padding: 1em;
  text-align: center;
}

.product img {
  width: 100%;
  height: auto;
}
